.footer {
    background: #202020;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 50px 70px;
}

a {
    text-decoration: none;
}

.links {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 20px;
    cursor: pointer;
}

img {
    border-radius: 0;
}
